.wrap{
    width: 410px;
    height: 114px;
    position: absolute;
    right: 26px;
    top: 26px;
    background: rgba(255,204,204,.4);
    border-radius: 8px;
    transition: all linear .5s;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
    color: #fff;
}
.weather{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 16px;
    transition: all linear .3s;
    display: flex;
    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 1;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    position: relative;
}
.weather::before{
    content: '';
    height: 60px;
    width: 1px;
    background: #ffffff;
    position: absolute;
    left: -10px;
    top: 50%;
    margin-top: -30px;
}
.top{
    /* justify-content: center; */
    align-items: center;
    width: 100%;
    display: flex;
}
.temp{
    font-weight: bold;
    font-size: 24px;
    display: inline-block;
}
.cond{
    font-size: 16px;
    color: #ffffff;
    display: inline-block;
    margin-left: 20px;
}
.timeBoxs{
    display: flex;
    flex: 1;
    flex-direction: column; 
    justify-content: center;
}
.time{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    font-weight: bold;
    margin: 0;
}
.date{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.weather .weatherIcon{
    width: 30px;
    height: 30px;
    margin-left: 14px;
    display: inline-block;
}
.bottom{
    width: 100%;
    text-align: left;
}