body,html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#root{
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchBox{
    width: 670px;
    height: 50px;
    /* border: 1px solid #fcc; */
    border-radius: 25px;
    position: absolute;
    left: 50%;
    top: 360px;
    margin-left: -335px;
    transition: all linear .3s;
    box-shadow: 0 0 8px rgba(255, 204, 204);
    z-index: 12;
}
.searchInput{
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding: 0 20px;
    color: #333;
    font-size: 16px;
    font-family: 'Microsoft Yahei';
    border-radius: 25px;
}
.birthday{
    position: absolute;
    left: 10px;
    top: -122px;
    z-index: 1;
    /* transition: loopMove .6s ease-in; */
}
.move{
    -webkit-animation: loopMove 4.6s linear infinite;
            animation: loopMove 4.6s linear infinite;
}
img{
    display: block;
    /* width: 120px; */
    height: 120px;
    border-radius: 6px; 
}
.text{
    position: absolute;
    left: 10px;
    top: 30px;
    color: #ffffff;
}
@-webkit-keyframes loopMove{
    0%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    25%{
        -webkit-transform: translateX(205px);
                transform: translateX(205px);
    }
    50%{
        -webkit-transform: translateX(420px);
                transform: translateX(420px);
    }
    75%{
        -webkit-transform: translateX(205px);
                transform: translateX(205px);
    }
    100%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
@keyframes loopMove{
    0%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    25%{
        -webkit-transform: translateX(205px);
                transform: translateX(205px);
    }
    50%{
        -webkit-transform: translateX(420px);
                transform: translateX(420px);
    }
    75%{
        -webkit-transform: translateX(205px);
                transform: translateX(205px);
    }
    100%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
.wrap{
    width: 410px;
    height: 114px;
    position: absolute;
    right: 26px;
    top: 26px;
    background: rgba(255,204,204,.4);
    border-radius: 8px;
    transition: all linear .5s;
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
    color: #fff;
}
.weather{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 16px;
    transition: all linear .3s;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}
.weather::before{
    content: '';
    height: 60px;
    width: 1px;
    background: #ffffff;
    position: absolute;
    left: -10px;
    top: 50%;
    margin-top: -30px;
}
.top{
    /* justify-content: center; */
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    display: -webkit-flex;
    display: flex;
}
.temp{
    font-weight: bold;
    font-size: 24px;
    display: inline-block;
}
.cond{
    font-size: 16px;
    color: #ffffff;
    display: inline-block;
    margin-left: 20px;
}
.timeBoxs{
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column;
            flex-direction: column; 
    -webkit-justify-content: center; 
            justify-content: center;
}
.time{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 22px;
    font-weight: bold;
    margin: 0;
}
.date{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.weather .weatherIcon{
    width: 30px;
    height: 30px;
    margin-left: 14px;
    display: inline-block;
}
.bottom{
    width: 100%;
    text-align: left;
}
.gifBox{
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 80px;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.gifItem{
    height: 100%;
    display: -webkit-flex;
    display: flex;

}
.gifItem img{
    display: block;
    height: 100%;
}
.App{
    width: 100%;
    height: 100%;
    background:url(/static/media/background.e3359e12.png) no-repeat;
    background-position: center bottom; 
    overflow: hidden;
}
*{
    margin: 0;
    padding: 0;
}
