.searchBox{
    width: 670px;
    height: 50px;
    /* border: 1px solid #fcc; */
    border-radius: 25px;
    position: absolute;
    left: 50%;
    top: 360px;
    margin-left: -335px;
    transition: all linear .3s;
    box-shadow: 0 0 8px rgba(255, 204, 204);
    z-index: 12;
}
.searchInput{
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding: 0 20px;
    color: #333;
    font-size: 16px;
    font-family: 'Microsoft Yahei';
    border-radius: 25px;
}
.birthday{
    position: absolute;
    left: 10px;
    top: -122px;
    z-index: 1;
    /* transition: loopMove .6s ease-in; */
}
.move{
    animation: loopMove 4.6s linear infinite;
}
img{
    display: block;
    /* width: 120px; */
    height: 120px;
    border-radius: 6px; 
}
.text{
    position: absolute;
    left: 10px;
    top: 30px;
    color: #ffffff;
}
@keyframes loopMove{
    0%{
        transform: translateX(0px);
    }
    25%{
        transform: translateX(205px);
    }
    50%{
        transform: translateX(420px);
    }
    75%{
        transform: translateX(205px);
    }
    100%{
        transform: translateX(0px);
    }
}